.dev-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100vh;
  width: 100vw;
}
@media only screen and (max-width: 600px) {
  .dev-grid {
    flex-direction: column;
    flex-wrap: inherit;
  }
}

.dev-grid-half {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 50vh;
  width: 100vw;
}
@media only screen and (max-width: 600px) {
  .dev-grid-half {
    flex-direction: column;
    flex-wrap: inherit;
  }
}
@media only screen and (max-width: 600px) {
  .dev-grid-half {
    height: 100vh;
  }
}

.dev-grid-cell {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  text-align: center;
  vertical-align: middle;
  height: 50vh;
  width: 50vw;
  overflow: hidden;
}
@media only screen and (max-width: 600px) {
  .dev-grid-cell {
    height: 100vh;
    width: 100vw;
  }
}

.dev-grid-cell-three-quarters-height {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  text-align: center;
  vertical-align: middle;
  height: 75vh;
  width: 100vw;
}

.dev-grid-cell-one-quarter-height {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  text-align: center;
  vertical-align: middle;
  height: 25vh;
  width: 100vw;
}

/**
 * Based on https://codepen.io/saransh/pen/BKJun
 */
/**
 * Generate n random elements.
 */
/**
 * Send items upwards.
 */
@keyframes upwards-journey {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-2000px);
  }
}
/* Style the buttons that are used to open the tab content */
.tabs {
  display: flex;
  align-items: center;
  text-align: center;
}
.tabs .tabs-navigation {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 1;
  text-align: center;
}
.tabs .tabs-navigation button {
  background: inherit;
  border: none;
  color: #a8a8a8;
  cursor: pointer;
  margin: 0 30px;
  outline: none;
  padding: 15px;
  transition: 0.3s ease;
}
.tabs .tabs-navigation button::after {
  background: #a8a8a8;
  content: "";
  display: block;
  height: 2px;
  margin-top: 5px;
  transition: 0.2s ease;
  width: 0;
}
.tabs .tabs-navigation button:hover {
  color: #a8a8a8;
}
.tabs .tabs-navigation button.active {
  color: #ffffff;
}
.tabs .tabs-navigation button.active::after {
  background: #ffffff;
  width: 100%;
}

.tooltip {
  position: relative;
}
.tooltip .tip {
  background-color: #ffffff;
  border-radius: 10px;
  color: #a8a8a8;
  left: 29px;
  opacity: 0;
  padding: 15px 30px;
  position: absolute;
  top: -15px;
  transition: opacity ease-in-out 0.3s;
  display: none;
  white-space: nowrap;
  width: 70px;
}
.tooltip:after {
  border: solid 7px transparent;
  border-right-color: #FFF;
  content: "";
  left: 15px;
  opacity: 0;
  position: absolute;
  top: 2px;
  transition: opacity ease-in-out 0.3s;
  display: none;
}
.tooltip:hover {
  cursor: pointer;
}
.tooltip:hover .tip {
  display: inline;
  opacity: 1;
  visibility: initial;
}
.tooltip:hover:after {
  display: block;
  opacity: 1;
  visibility: initial;
}

